* {
  box-sizing: border-box;
}

.chat__seccion-container {
  flex-direction: column;
  height: 100%;
  padding: 10px;
  display: flex;
}

.chat-titulo {
  align-self: center;
}

.chat__seccion-container :last-child {
  margin-bottom: 10px;
}

.chatPage {
  flex-direction: column;
  flex-grow: 1;
  display: flex;
}

.chat__seccion-messages {
  grid-template-rows: 1fr max-content;
  gap: 10px;
  max-width: 500px;
  height: 100%;
  display: grid;
}

.chat__seccion-fomulario {
  grid-row: 2;
}

.chat__seccion-chat {
  border: 2px solid;
  border-radius: 5px;
  flex-direction: column;
  width: 100%;
  max-height: 370px;
  margin: 0 auto;
  padding: 10px;
  display: flex;
  overflow-y: auto;
}

.chat__seccion-chat::-webkit-scrollbar-thumb {
  background-color: #0000;
}

@media (width >= 524px) {
  .chat__seccion-messages {
    width: 495px;
    margin: 10px auto;
  }

  .chat__seccion-chat {
    max-height: 600px;
  }
}

.template__card {
  grid-template-rows: max-content 1fr;
  width: max-content;
  display: grid;
}

.template__card-propio {
  justify-self: end;
}

.template__card__nombre {
  grid-row: 1;
}

.template__card__mensaje {
  grid-row: 2;
}

.template__card__nombre {
  color: #a5a5a5;
  font-size: 14px;
}

.template__card__mensaje {
  overflow-wrap: break-word;
  background-color: #d8d8d8;
  border-radius: 5px;
  max-width: 300px;
  padding: 10px;
  font-size: 18px;
}

.mensajePropio {
  overflow-wrap: break-word;
  background-color: #b9e97c;
  border-radius: 5px;
  max-width: 300px;
  padding: 10px;
  font-size: 18px;
}

.eliminarNombre {
  display: none;
}

.correr {
  align-self: flex-end;
}

.verde {
  background-color: #b9e97c;
}

